import { QueryDate } from '../utils/types';
import CloseListButton from './CloseListButton';

type Props = {
  setIsPage: React.Dispatch<React.SetStateAction<string | null>>;
  setQueryDate: React.Dispatch<React.SetStateAction<QueryDate>>;
  setQueryKeyword: React.Dispatch<React.SetStateAction<string>>;
  closeListHandler: (event: any) => void
}

const Content = (props: Props) => {
  const { setQueryDate, setQueryKeyword, setIsPage, closeListHandler } = props;

  const handleSubmit = (e: any) => {

    e.preventDefault();

    const form = e.target;
    const formData = new FormData(form);

    const queryDate: QueryDate = [];

    formData.getAll('eventDate').forEach((date) => {
      // @ts-ignore
      queryDate.push(date);
    });

    setQueryDate(queryDate);
    setQueryKeyword(formData.get('keyword') as string);

    setIsPage('searchResults');

    form.reset();
  }

  return (
    <>
      <div id="search">
        <CloseListButton closeListHandler={closeListHandler} />
        <form method="post" onSubmit={handleSubmit}>
          <div className="content-title"><img className='calendar-logo' src='./img/calendar.svg' alt="" /></div>
          <div className="checkbox-group mts">
            <div className="item">
              <input type="checkbox" name="eventDate" className="checkbox" value="today" id="today" defaultChecked />
              <label htmlFor="today"></label>
            </div>
            <div className="item">
              <input type="checkbox" name="eventDate" className="checkbox" value="tomorrow" id="tomorrow" />
              <label htmlFor="tomorrow"></label>
            </div>
            <div className="item">
              <input type="checkbox" name="eventDate" className="checkbox" value="weekend" id="weekend" />
              <label htmlFor="weekend"></label>
            </div>
          </div>
          <div className="keyword-container">
            <input id="keyword" name="keyword" type="text" placeholder="" />
            <img className='search-logo' src='./img/search-gray.svg' alt="" />
          </div>
          <button id="submit-button"><img className='arrow-right' src='./img/arrow-white-right.svg' alt="" /></button>
        </form>
      </div>
    </>
  );
}

export default Content;
