export const targetIcon = (target: string) => {

  const baseDir = './img'
  const child = `${baseDir}/target-child.svg`
  const adult = `${baseDir}/target-adult.svg`
  const all = `${baseDir}/target-all.svg`

  const icon = {
    '': child,
    '': child,
    '': child,
    '': adult,
    '': adult,
    '': all,
  }

  // @ts-ignore
  return icon[target]
}